.hero {
	height:100%;
	background-repeat:no-repeat;
	background-size:cover;
	background-position:center;
	overflow: hidden;
	position: relative;


	&:before {
		content:'';
		width:100%;
		height:100%;
		background: blue;
		opacity: 0.05;
		display:block;
		position:absolute;
		left:0;
		top:0;
		z-index: 1;
	}
	.gallery{
		max-width: 100%;
		margin: 0 auto;
		img{
			width: 100%;
			margin: 0 auto;
		}	
		.gallery-subtitles {
		width: 100%;
		position: absolute;
		top:74%;
		transform:translate(-50%,-50%);
		left: 50%;
		text-align:center;
		h1 {
			font-size:96px;
			font-family:"VolvoBroad";
			color:$white;
			margin-top:20px;
		}
		.button-hero{
			.btn{
				border: 3px solid #FFFFFF;
				background-color: none;
				border-radius: 10px;
				letter-spacing: 1px;
				color: $light-grey;	
				font-size: 36px;
				font-family: "VolvoBroad";
				padding: 0 60px 0 60px;
				}
			}	 
		}
	}
	}


.header{
	position: fixed;
    height: 75px;
    width: 2000px;
    margin: 0 auto;
    z-index: 3;
	background: linear-gradient(rgb(77, 77, 77), #ffffff00);
	img{
		position: absolute;
		width: 100px;
		height: 100px;
		z-index: 2;
		left: 40px;
		top: 30px;
	}
	ul{
		width: 485px;
		height: 4000px;
		margin: 2px auto;
		left: 176px;
		top: 59px;
		position: absolute;
		li{
			display: inline-block;
			margin: 10px; 
			margin-right: 20px;
			font-family: VolvoSansLight;
			font-size: 18px;
			
			a{
				text-decoration: none;
				color:whitesmoke;
				margin-right: 20px;
			}
		}
	}
}