/* Fonts */
$main-font: 'VolvoSansLight';
$VolvoSans: 'VolvoSans';
$VolvoSansBold: 'VolvoSansBold';

/* Colors */
$main-color:#64c29e;
$title-color:#7e7f80;
$text-color:#8e9193;
$light-grey:#F4F4F4;
$dark-color:#282f35;
$white:#FFF;
$digital-blue: #5C92BB;
$background-color:#003057;

@font-face {
    font-family: VolvoSansLight;
    src: url(../fonts/VolvoSansLight.ttf);
  }
@font-face {
    font-family: VolvoSans;
    src: url(../fonts/VolvoSans.ttf);
}
@font-face {
  font-family: VolvoSansBold;
  src: url(../fonts/VolvoSans_Bold.ttf);
}

body {
	height:100%;
}
html {
	height:100%;
}

.btn{
    display: inline-block;
    background: transparent;
    border: 1px solid $main-color;
    color: $main-color;
    font-size: 24px;
    text-align: center;
    padding: 0 20px;
    border-radius: 30px;
    font-family: $main-font;
    text-transform: uppercase;
    text-decoration: none;
    margin-top: 20px;
    line-height: 50px;
}