.footer{
    width: 100%;
    height: 300px;
    position: relative;
    background-color: $background-color;
    .center{
    position: relative;
    margin-top:50px; 
    top: 15%;
    text-align:center;
    margin: 0 auto;
    max-width: 90%;
    .wrap{
        position: relative;
        font-size: 0;
        width: 60%;
        left: 27%;
        display: inline-block;
        vertical-align: middle;
        .searchTerm {
            display: inline-block;
            margin-right: 10px;
            padding: 12px;
            padding-left: 30px;
            padding-right: 130px;
            font-size: 22px;
            color:#fff;
            background-color: rgba(196, 196, 196, 0.5);
            border: 3px solid #fff;
            border-radius: 10px;
            font-family: "VolvoBroad";
            }
          .searchButton { 
            text-align:center;
            display: inline-block;
            background: none;
            font-family: "VolvoBroad";
            letter-spacing: 0.1em;
            color: #fff;
            cursor: pointer;
            margin-left: 50px;
            font-size: 22px;
            padding: 12px;
            padding-left: 30px;
            padding-right: 30px;
            border: 3px solid #ffffff;
            border-radius: 10px;
            }   
        }
        .icons{
        display: inline-block;
        list-style-type:none;
        text-align: center;
        position: relative;
        font-size: 0;
        right: 58%;
        vertical-align: middle;
        a{
        font-size: 48px;
        margin-right: 27px;
        color: white;
        text-decoration: none;
        margin-top: 50px;
        top: 15%;
        &:nth-of-type(3){
            margin-top: 15px;
            }
        }
    }
        .footer_1{
            h1{
                color: #fff;
                display: inline-block;
                font-size: 14px;
                vertical-align: middle;
                margin-right: 100px;
                margin-top: 40px;
                }
            h2{
                font-size: 14px;
                color: #fff;
                margin-top: 75px;
                letter-spacing: 1px;
                }
            }
    }   
    
}
