.contactos{
	width: 100%;
    background-color: #000000;
    font-size: 0;
	.container {
        text-align: center;
        margin: 0 auto;
        max-width: 80%;
        width: 100%;
        padding: 100px 0;
        position: relative;
        border: 1px solid #000;
        i{
            font-size: 48px;
            display: inline-block;
            color: #F4F4F4;
        }
        h1{
            color: #F4F4F4;
            font-family: "VolvoSans";
            font-size: 18px;
            line-height: normal;
            margin-left: 20px;
            margin-bottom: 10px;
            display: inline-block;
            margin-right: 134px;
            text-align: left;
            &:nth-of-type(2){
                vertical-align: text-bottom;
            }
            &:nth-of-type(3){
                vertical-align: text-bottom;
                margin-right: 0px;
            }
        }
    }
}