.split_2 {
	width: 100%;
	height: 90%;
	font-size: 0;
	.left_2{
	  height: 100%;
	  width: 70%;
	  display: inline-block;
	  vertical-align: top;
	  background-image:url('../img/5.jpg');
	  background-repeat:no-repeat;
	  background-size:cover;
	  background-position:center;
	  opacity: 0.9;
	  }
	.right_2{
		height: 100%;
		width: 30%;
		display: inline-block;
		background-color: #000000;;
		.container{
		height: 35%;
		max-width: 80%;
		margin: 0 auto;
		position: relative;
		text-align: center;
		margin-top: 30px;
		bordser: 1px solid #000;
		.title{
			position: relative;
			text-align: left;
			bsorder: 1px solid #000;
			i{
				font-size: 40px;
				display: inline-block;
				vertical-align: middle;
				color: $light-grey;
			}
			h1{
				color: $light-grey;
				font-family: "VolvoBroad";
				font-size: 25px;
				line-height: 35px;
				margin-left: 14px;
				margin-bottom: 10px;
				display: inline-block;
				vertical-align: middle;
				}
			h2{
				color: $light-grey;
				font-family: "VolvoSans";
				font-size: 15px;
				margin-top: 15px;
				&:nth-of-type(2){
					margin-top: 5px;
					}
				}	
		} 
		.button{
			margin: 0 auto;
			margin-top: 10px;
			.btn{
				width: 100%;
				border: 3px solid #FFFFFF;
				background-color: $background-color;
				border-radius: 10px;
				letter-spacing: 0.1em;
				color: #ffffff;	
				font-size: 22px;
				font-family: "VolvoBroad";
				}
			}	 
		}	  
		.container2{
			height: 65%;
			max-width: 80%;
			margin: 0 auto;
			position: relative;
			text-align: center;
			margin-top: 25px;
			borsder: 1px solid #000;
			.title{
				position: relative;
				text-align: left;
				borsder: 1px solid #000;
				i{
					font-size: 40px;
					display: inline-block;
					vertical-align: middle;
					color: $light-grey;
				}
			h1{
				color: $light-grey;
				font-family: "VolvoBroad";
				font-size: 25px;
				line-height: 35px;
				margin-left: 14px;
				margin-bottom: 10px;
				display: inline-block;
				vertical-align: middle;
				}
			}
			.wrap{
			margin-top: 5px;
			.searchTerm {
				display: inline-block;
				margin-bottom: 10px;
				padding: 12px;
				padding-left: 30px;
				text-align: left;
				width: 100%;
				font-size: 17px;
				font-family: "VolvoBroad";
				color: $light-grey;
				background-color: rgba(196, 196, 196, 0.5);
				border: 3px solid #FFFFFF;
				border-radius: 10px;
			}
			.searchButton { 
				display: inline-block;
				background: #003057;
				font-family: "VolvoBroad";
				letter-spacing: 0.1em;
				color: #fff;
				cursor: pointer;
				font-size: 22px;
				padding: 12px;
				border: 3px solid #FFFFFF;
				text-align: center;
				width: 100%;
				border-radius: 10px;
			}   
		}
	}
		}
	}
