.split {
  width: 100%;
  height: 90%;
  font-size: 0;
  .left{
    height: 100%;
    width: 70%;
    display: inline-block;
    vertical-align: top;
    background-image:url('../images/hero.jpg');
    background-repeat:no-repeat;
    background-size:cover;
    background-position:center;
    opacity: 0.9;
    }
  .right{
    height: 100%;
    width: 30%;
    display: inline-block;
    background-color: #000;
    .container{
      height: 35%;
      max-width: 80%;
      margin: 0 auto;
      position: relative;
      text-align: center;
      margin-top: 20px;
      borsder: 1px solid #000;
      .title{
        position: relative;
        text-align: left;
        bordser: 1px solid #000;
        i{
          font-size: 48px;
          display: inline-block;
          vertical-align: middle;
          color: $light-grey;
        }
      h1{
        color:$light-grey;
        font-family: "VolvoBroad";
        font-size: 30px;
        line-height: 35px;
        margin-left: 14px;
        margin-bottom: 10px;
        display: inline-block;
        vertical-align: middle;
        }
      }
        .wrap{
          position: relative;
          font-size: 0;
          margin-top: 5px;
          input{
            text-align: left;
          }
        .searchTerm {
          display: inline-block;
          margin-bottom: 10px;
          padding: 12px;
          text-align: center;
          width: 100%;
          font-size: 22px;
          font-family: "VolvoBroad";
          color: $light-grey;
          background-color: rgba(196, 196, 196, 0.5);
          border: 3px solid #FFFFFF;
          border-radius: 10px;
          }
        .searchButton { 
          display: inline-block;
          background: #003057;
          font-family: "VolvoBroad";
          letter-spacing: 0.1em;
          color: #fff;
          cursor: pointer;
          font-size: 26px;
          padding: 12px;
          border: 3px solid #FFFFFF;
          text-align: center;
          width: 100%;
          border-radius: 10px;
          }   
      }
  }
  .container2{
      height: 30%;
      max-width: 80%;
      margin: 0 auto;
      text-align: center;
      border: 1px solid #000;
      .container-text{
        text-align: left;
        margin-top: 30px;
        p{
          color: $light-grey;
          font-family: $VolvoSans;
          display: inline-block;
          font-size: 18px;
         
        }
        :nth-child(2){
          margin-top: 72px;
          margin-left: 20px;
          margin-right: 20px;
        }
      }
      .title{
        background: silver;
        position: relative;
        text-align: left;
        borsder: 1px solid #000;
        width: 60%;
        i{
          font-size: 48px;
          display: inline-block;
          vertical-align: middle;
          color: $light-grey;
        }
        h1{
          color:$light-grey;
          font-family: "VolvoBroad";
          font-size: 30px;
          line-height: 35px;
          margin-left: 14px;
          margin-bottom: 10px;
          display: inline-block;
          vertical-align: middle;
          }
      }

      .mapa{
        display: inline-block;
        position: relative;
        &:nth-of-type(2){
            margin-left: 20px;
          }
        i{
          position: absolute;
          font-size: 30px;
          left: 35px;
          bottom: 15px;
          color: $light-grey;
        }
        a{
          color: $light-grey;
          font-family: $VolvoSans;
          display: inline-block;
          font-size: 16px;
          text-decoration: none;
          
          }
      }
    }
  }
}
